import React, { useState } from 'react'
import TodoForm from './TodoForm'
import './TodoList.css'

function Todo(props) {

    return (
        <ul className='todo-list'>
            {props.list.map( item => {
                const checkClass = item.isComplete ? 'fa fa-circle fa-circle-done' : 'fa fa-circle'
                const todoClass = item.isComplete ? 'todo todo-done' : 'todo'
                const removeClass = item.isComplete ? 'fa fa-times fa-times-done' : 'fa fa-times'
                const inputClass = item.isComplete ? 'todo-input todo-input-done' : 'todo-input'
                return (
                    <li className='todo-item' key={item.id}>
                        <i className={checkClass} onClick={() => props.handleComplete(item.id)} />
                        <div className={todoClass}>
                            {item.value}
                        </div>
                        <div className='todo-remove'>
                            <i className={removeClass} onClick={() => props.handleRemove(item.id)}/>
                        </div>
                    </li>
                )
            })}
        </ul>
    )
}

export default Todo