import React from 'react'
import './Header.css'

function Header() {
    const date = new Date()
    const options = {weekday: 'long', month: 'short', day: 'numeric'}
    return (
        <header>
            <h1>TODO LIST</h1>
            <p>{date.toLocaleDateString('en-US', options)}</p>
        </header>
    )
}

export default Header
